import React,{ useState, useEffect } from 'react';
import styled , { css }from 'styled-components';
import { Link } from "react-router-dom";
import RemoveItemFromBasket from '../../ActionButton/RemoveItemFromBasket';
import { useInView } from "react-intersection-observer";
import Skeleton from '@mui/material/Skeleton';


const MiniCard = ({image, title, price, id, description, removeFromBacket, count}) => {
    
    const { ref, inView } = useInView({
        threshold: 0.4,
        triggerOnce: true,
    });

    const [propsBtn , setPropsBtn] = useState(false);

    useEffect(() => {
        removeFromBacket ? setPropsBtn(true) : setPropsBtn(false)
    },[removeFromBacket])

    let itemId = id;
  return (
    <>
        <CardWrapper ref={ref} active={propsBtn ? 'active' : null}>
            {inView ?
            <>
            <Card to={`/product/${itemId}`}>
                <Container>
                    <ContainerImg>
                        <img src={image} alt="img" />
                    </ContainerImg>

                    <p>{title}</p>
                    {removeFromBacket ? null : <span>{price} $</span>}
                    
                </Container>
            </Card>
            {removeFromBacket ? 
                    <AbsoluteContainer>
                        <RemoveItemFromBasket 
                            price={price} 
                            id={id} 
                            count={count}/>
                    </AbsoluteContainer>
                    :
                    null}
            </>
            :
        
            <RevorkSkeleton
                sx={{ bgcolor: 'grey.900' }}
                variant="rectangular"
            />
         
        }

        </CardWrapper>
    </>
  )
}
const RevorkSkeleton = styled(Skeleton)`
    height: 100% !important;
    width: 100%;
`

const CardWrapper = styled.div((props) => {
    let margin = +'0';
    let borderRight = '1';

    if(props.active === 'active'){
        margin = 60;
        borderRight = 'none';
    }
    return css`
    /* z-index: 5; */
    position:relative;
    height: 270px;
    width: 180px;
    border: 1px solid #c8cfd8;
    border-right: ${borderRight};
    margin: 0.125rem;
    margin-right:${margin}px;`
});

const Card = styled(Link)`
    display: block;
    width: 100%;
    height: 100%;
    background-color: #fff;
    padding: 0.625rem;
    font-size: 14px;
        color:#000;
        text-decoration: none;
        :hover{
            color:#f84147;
            text-decoration: underline;
        }
`
const ContainerImg = styled.div`
        max-height: 160px;
        min-height: 160px;
        display: flex;
        justify-content:center;
        align-items:center;
        margin-bottom:0.5rem;
        :hover{
            cursor:pointer;
        }
    img{
        max-width: 100%;
        max-height: 100%;
    }
`
const Container = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;
    padding-bottom: 1.875rem;
    span{
        font-size: 20px;
        color:#f84147;
        position: absolute;
        right: 0;
        bottom: 0;
    }
`
const AbsoluteContainer = styled.div`
    position:absolute;
    width: 60px;
    height: 270px;
    left:100%;
    bottom:-1px;
    border: 1px solid #c8cfd8;
    border-left: none;
    padding: 4px 2px;
`
export default MiniCard