import React from 'react';
import styled,{css} from 'styled-components';
import { Wrapper } from '../Styled/GlobalStyled';
import AppBar from '@mui/material/AppBar';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import { Link } from "react-router-dom";
import ButtonGroup from '@mui/material/ButtonGroup';
import AvatarCash from './Avatar/AvatarCash';
import Paper from '@mui/material/Paper';
import logo from '../Main/Sliders/SliderMain/SliderImages/logo.png';
import Search from './Search/Search';
import HinnedMenu from './HiddenBar/HinnedMenu';


const Header = ({dataForHeader, setPodlojka}) => {
  return (
    <StickyHeader>
        <Box sx={{position:'sticky',flexGrow: 1 }} >
            <AppBar style={{backgroundColor: '#000'}} position="static">
                <Wrapper>
                    <WrapContent style={{minHeight: '50px'}}>

                    <Typography component="div">
                        <LogoImg style={{backgroundImage: `url(${logo})`}} to="/" />
                    </Typography>

                    <Search
                        dataForHeader={dataForHeader}
                        setPodlojka={setPodlojka}
                     />

                    <ButtonGroup aria-label="outlined primary button group">
                        <AvatarCash />
                    </ButtonGroup>

                    </WrapContent>
                </Wrapper>
            </AppBar>
        </Box>
        <HinnedMenu />
    </StickyHeader>
  )
}
const FormWrap = styled(Paper)`
    margin: 4px 2px;
    max-height: 40px;
    div{
        input{
            height: auto;
            font-size:14px;
        }
    }
`
const LogoImg = styled(Link)`
    display: flex;
    width: 50px;
    height: 50px;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
`

const HeaderMenu = styled.div`
    position:sticky;
    left: 0;
    top:0;
    z-index:2;
    background-color: seagreen;
    height: auto;
    padding:15px 5px;
    margin-bottom: 5px;
`
const WrapContent = styled(Toolbar)`
    display: flex !important; 
    width: 100%;
    justify-content: space-between !important;
    align-items: center;
`
const StickyHeader = styled.div((props) => {

    return css`
        position: sticky;
        left: 0;
        top:0;
        z-index:10;
        width: 100%;
    `
})


export default Header