import React, { useState, useEffect , createContext} from 'react'
import db from '../Json/db.json';
import { Routes, Route } from "react-router-dom";
import Korzina from './Korzina/Korzina';
import Backdrop from '@mui/material/Backdrop';
import CircularProgress from '@mui/material/CircularProgress';
import AlertUser from './AlertUser/AlertUser';
import CardsListItems from './CardsListItems/CardsListItems';
import ContextData from '../Context/ContextData';
import GeneretedLink from './GeneretedLink/GeneretedLink';

const {Provider} = ContextData;


const Main = ({setCounter, setDataForHeader}) => {

    const [data, setData] = useState([]);
    const [tovar, setTovar] = useState([]);

    const [alertState, alertSetState] = useState({
        open: false,
        vertical: 'top',
        horizontal: 'center',
        location: "add"
    });

    let filtered;
    
    const RemoveDublicate = () => {
        if(tovar !== null){
            const ids = tovar.map(o => o.id);
            filtered = tovar.filter(({id}, index) => !ids.includes(id, index + 1))
        }
    }

    useEffect(() => {
        const setLocal = async () => {
            if(await tovar.length !== 0){
                localStorage.setItem('items', JSON.stringify(tovar));
                RemoveDublicate()
            }else{
                localStorage.removeItem('items')
            }
        }
        setLocal()
    },[tovar])
    useEffect(() => {
        const items = JSON.parse(localStorage.getItem('items'));
        if (items) {
            setTovar(items);
        }
      }, []);
    
      useEffect(() => {
        setDataForHeader((prev) => prev = data)
      },[data])

    const [openLoad, setOpenLoad] = useState(false);

    const handleCloseLoad = () => {
        setOpenLoad(false);
    };

    RemoveDublicate();


    const CountTovar = () => {
        if(filtered !== undefined){
            setCounter((prev) => prev = filtered.reduce((acc, item) => acc+=item.count,0))
        }
    }
    // useEffect(() => {
    //     const FetchCards = () => {
    //         let countItems = 10;
    //         let url = `https://dummyjson.com/carts/?limit=${countItems}`;
    //         fetch(url)
    //             .then(res => res.json())
    //             .then(res => setData(res.carts))
    //             .then(() => handleCloseLoad())
    //     }
    //     FetchCards();
    //     CountTovar();
    // },[])
    useEffect(() => {
        setData(db);
        CountTovar();
    },[])
    useEffect(() => {
        CountTovar();
    },[tovar])


  return (
    <Provider value={data}>
            <div>
                <AlertUser
                    tovar={tovar}
                    state={alertState} 
                    setState={alertSetState} />
                <Backdrop
                    sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
                    open={openLoad}
                    >
                <CircularProgress color="inherit" />
            </Backdrop>
                <Routes>
                    <Route 
                        path="*" 
                        element={<CardsListItems 
                            tovar={tovar}
                            setTovar={setTovar}
                            data={data}
                            setData={setData}
                            alertState={alertState} 
                            alertSetState={alertSetState}/>} />
                    <Route 
                        path="korzina" 
                        element={<Korzina
                            filtered={filtered}
                            tovar={tovar}
                            setTovar={setTovar}
                            alertSetState={alertSetState} />} />
                    <Route 
                        path="/product/:id" 
                        element={<GeneretedLink
                            tovar={tovar}
                            setTovar={setTovar}
                         />} />
                </Routes>
            </div>
    </Provider>
  )
}

export default Main