import React, {useState, useEffect } from 'react'
import Typography from '@mui/material/Typography';
import Breadcrumbs from '@mui/material/Breadcrumbs';
import {Link} from 'react-router-dom';
import HomeIcon from '@mui/icons-material/Home';
import styled from 'styled-components';

const Pagination = ({firstStep, secondStep, title, typeCategory, typeGender, thirdStep}) => {

  const [firstStepState , setFirstStepState] = useState('');
  const [secondStepState, setSecondStepState] = useState('');

  const checkFirstStep = () => {
    if(typeCategory === 'clothes'){
      setFirstStepState('одежда')
    }
    if(typeCategory === 'forHome'){
      setFirstStepState('товары для дома')
    }
    if(typeCategory === 'food'){
      setFirstStepState('еда')
    }
    if(typeCategory === 'tech'){
      setFirstStepState('техника')
    }
  }

  const checkSecondStep = () => {
    if(typeGender === 'man'){
      setSecondStepState('мужчина')
    }
    if(typeGender === 'woman'){
      setSecondStepState('женщина')
    }
  }

  useEffect(() => {
    checkFirstStep();
    checkSecondStep();
  },[typeGender, typeCategory])



  
  return (
    <div role="presentation">
    <Breadcrumbs aria-label="breadcrumb">
      <LinkColorize 
        underline="hover" 
        color="inherit"
        to={firstStep}>
       <HomeIcon />
      </LinkColorize>

      <LinkColorize
        underline="hover"
        color="inherit"
        to={secondStep}
      >
        {firstStepState}
      </LinkColorize>
      {typeGender && typeGender.length > 0 ?
          <LinkColorize
            underline="hover"
            color="inherit"
            to={`${secondStep}/${typeGender}`}
          >
            {secondStepState}
          </LinkColorize>
      : null}

      <Typography color="text.primary">...</Typography>
      {/* <Typography color="text.primary">{title}</Typography> */}
    </Breadcrumbs>
  </div>
  )
}

const LinkColorize = styled(Link)`
  color:#3e77aa;
  text-decoration: none;
  :hover{
    color:#f84147;
    text-decoration: underline;
  }
`

export default Pagination