import React, { useState, useEffect, useContext} from 'react';
import { Routes, Route  } from "react-router-dom";
import styled from 'styled-components';
import ItemCard from '../CardOfItem/ItemCard';
import Sidebar from '../Sidebar/Sidebar';
import Clothes from './RoutesList/Clothes/Clothes';
import Food from './RoutesList/Food';
import Tech from './RoutesList/Tech';
import ForHome from './RoutesList/ForHome';
import ContextData from '../../Context/ContextData';
import MainAllItems from '../MainAllItems/MainAllItems';
import ContextTovar from '../../Context/ContextTovar';

const {Provider} = ContextTovar;

const CardsListItems = ({
    tovar, 
    setTovar, 
    setData, 
    alertState, 
    alertSetState
}) => {

    const [tovarContext , setTovarContext] = useState({});

    useEffect(() => {
        setTovarContext({tovar: tovar , setTovar : setTovar})
    },[tovar])
   
    const [wrapData , setWrapData] = useState([]);

    const data = useContext(ContextData);

    useEffect(() => {
        if(data){
            setWrapData([...data]);
        }
    },[data])

    return(
        <Provider value={tovarContext}>
            <WrapBlock>
                <Sidebar
                    wrapData={wrapData}
                    setWrapData={setWrapData}
                />
                
                <Routes>
                    <Route 
                        path="/"
                        element={<MainAllItems />} />
                    <Route 
                        path="/food" 
                        element={
                            <Food
                                wrapData={wrapData}
                                tovar={tovar}
                                setTovar={setTovar}
                                alertState={alertState}
                                alertSetState={alertSetState} />
                            } />
                    <Route 
                        path="/clothes//*"
                        element={<Clothes 
                                wrapData={wrapData}
                                tovar={tovar}
                                setTovar={setTovar}
                                alertState={alertState}
                                alertSetState={alertSetState}/>} />
                    <Route 
                        path="/tech" 
                        element={<Tech 
                                wrapData={wrapData}
                                tovar={tovar}
                                setTovar={setTovar}
                                alertState={alertState}
                                alertSetState={alertSetState}/>} />
                    <Route 
                        path="/forHome" 
                        element={<ForHome 
                                wrapData={wrapData}
                                tovar={tovar}
                                setTovar={setTovar}
                                alertState={alertState}
                                alertSetState={alertSetState}/>} />
                </Routes>

            </WrapBlock>
        </Provider>
    )
}
export default CardsListItems

const WrapBlock = styled.div`
    display:flex;
    position:relative;
`