import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y } from 'swiper';
import styled from 'styled-components';
import SidebarButton from '../../Main/Sidebar/SidebarButton/SidebarButton';
import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import 'swiper/css/scrollbar';

const HinnedMenu = () => {
  return (

    <WrapContent>
            <Swiper
      spaceBetween={10}
      slidesPerView={2.5}


    >
        <SwiperSlide>
            <SidebarButton
                text={'Бытовая техника'}
                linkWhere='tech'/>
        </SwiperSlide>
        <SwiperSlide>
            <SidebarButton
                text={'Одежда'}
                linkWhere='clothes'/>
        </SwiperSlide>
        <SwiperSlide>
            <SidebarButton
                text={'Товары для дома'}
                linkWhere='forHome'/>
        </SwiperSlide>
        <SwiperSlide>
        <SidebarButton
            text={'Еда'}
            linkWhere='food'/>
        </SwiperSlide>
    </Swiper>

    </WrapContent>
  )
}



const WrapContent = styled.div`
    display: none;
    background-color:#fff;
    @media only screen and (max-width: 768px){
        display: block;
        position: relative;
        z-index: -1;
        /* flex-wrap: wrap; */
        a{
            min-width:auto !important;
            background-color: #c9c9c0;
            margin: 5px;
            border-radius: 20px;
        }
    }
`

export default HinnedMenu


