import React, { useState } from 'react'
import ContextCounter from './components/Context/ContextCounter';
import Header from "./components/Header/Header";
import Main from "./components/Main/Main";
import ShadowScreen from './components/ShadowScreen/ShadowScreen';
import { Wrapper } from "./components/Styled/GlobalStyled";
import styled,{css} from 'styled-components';

const {Provider} = ContextCounter;

function App() {
  const [dataForHeader, setDataForHeader] = useState([{test: 'test'}]);
  const [counter, setCounter] = useState(0);
  const [podlojka, setPodlojka] = useState(false);

  return (
    <Provider value={counter}>
      <OverfloHiddenBox active={podlojka}>
        <Header
          counter={counter}
          dataForHeader={dataForHeader}
          setPodlojka={setPodlojka}/>
        <Wrapper>
          <Main
            setCounter={setCounter} 
            setDataForHeader={setDataForHeader}/>
        </Wrapper>
    </OverfloHiddenBox>
    <ShadowScreen 
        setPodlojka={setPodlojka}
        podlojka={podlojka}/>
    </Provider>
  );
}

