import React,{ useEffect, useState } from 'react';
import styled from 'styled-components';
import ContextTovar from '../../Context/ContextTovar';
import ItemCard from '../CardOfItem/ItemCard';

const {Provider} = ContextTovar;

const Korzina = ({tovar, setTovar, filtered, alertSetState}) => {

  const [load, setLoad] = useState(true);

  const [tovarContext , setTovarContext] = useState({});

  useEffect(() => {
    setTovarContext({tovar: tovar , setTovar : setTovar})
  },[tovar])

  useEffect(() => {
    if(tovar){
      setLoad(false)
    }
  },[tovar])

  return(
    <>
    <Provider value={tovarContext}>
      {filtered !== undefined && filtered.length >= 1 ? 
        <CountText>Общая стоимость товаров :
          <span>
            {filtered ? `${filtered.reduce((acc, el) => acc += el.price * el.count,0)} $` : null}
          </span>
        </CountText>:  
        <CountText>
          No items here...
        </CountText>}

        <WrapFlex>
          {load ? <span>Loading...</span> : typeof filtered !== "undefined" ? 
              filtered.map((el, id) => 
                <ItemCard 
                    alertSetState={alertSetState}
                    key={el.id}
                    title={el.title}
                    id={el.id}
                    tovar={tovar}
                    setTovar={setTovar}
                    bye={false}
                    price={el.price}
                    count={el.count} />
                ): null}
        </WrapFlex>
      </Provider>
    </>
  )
}
const CountText = styled.div`
  text-align: center;
  font-size:25px;
  font-weight: 700;
`

const WrapFlex = styled.div`
  display: flex;
  flex-wrap:wrap;
  padding: 10px;
  @media only screen and (max-width: 768px){
    justify-content: center;
  }
`
export default Korzina