import React ,{ useContext } from 'react';
import MiniCard from '../MiniCard/MiniCard';
import SliderMain from '../Sliders/SliderMain/SliderMain';
import styled from 'styled-components';
import ContextData from '../../Context/ContextData';

const MainAllItems = () => {

    const data = useContext(ContextData);


  return (
    <WrapAlign>
        <SliderMain />
        <h4>Список всех товаров</h4>
        <ContainerMiniCards>
            {
                data.map((el, id) => 
                <MiniCard 
                    key={id} 
                    image={el.products[0].img} 
                    title={el.products[0].title} 
                    description={el.products[0].description} 
                    price={el.products[0].price}
                    id={el.id} />)
            }
        </ContainerMiniCards>
    </WrapAlign>
  )
}

const WrapAlign = styled.div`
    width: calc(100% - 12.5rem);
    padding: 10px;
    @media screen and (max-width: 768px){
        width: 100%;
    }
`
const ContainerMiniCards = styled.div`
    display: flex;
    flex-wrap:wrap;
    margin: 10px 0;
`

export default MainAllItems

