import React from 'react';
import styled from 'styled-components';
import ItemCard from '../../CardOfItem/ItemCard'
import SortingCards from '../SortingCards/SortingCards';

const Food = ({
    wrapData,
    tovar,
    setTovar,
    alertState,
    alertSetState,
}) => {
  return (
    <WrapForCardsPlusSorting>
        <SortingCards />
        <ListWrap>
            {wrapData.length > 0 ? 
                wrapData
                .filter(el => el.products[0].type.type === "food")
                .map((el, id) => 
            <ItemCard 
                key={el.id}
                title={el.products[0].title}
                id={el.id}
                description={el.products[0].description}
                img={el.products[0].img}
                tovar={tovar}
                setTovar={setTovar}
                bye={true}
                price={el.products[0].price}
                alertState={alertState}
                alertSetState={alertSetState} />
            ) : <span>Loading...</span>}
        </ListWrap>
    </WrapForCardsPlusSorting> 
  )
}
const WrapForCardsPlusSorting = styled.div`
    padding: 10px 5px 150px 20px ;
`
const ListWrap = styled.div`
    display: flex;
    flex-wrap: wrap;
`



export default Food