import React,{ useState, useEffect } from 'react';
import ItemCard from '../../../../CardOfItem/ItemCard';
import Pagination from '../../Pagination/Pagination';
import styled from 'styled-components';

const ClothMan = ({
    wrapData,
    tovar,
    setTovar,
    alertState,
    alertSetState,
  }) => {

    const [newItems , setNewItems] = useState([]);

    useEffect(() => {
      if(wrapData.length > 0){
        setNewItems(wrapData.filter(el => el.products[0].type.type === "clothes"))
      }
    },[wrapData])

  return (
    <FlexColumn>
      <Pagination
        firstStep={'/'}
        secondStep={'/clothes'}
        title={'мужчина'}
        typeCategory={"clothes"}
      />
      <ListWrap>
      {newItems.length > 0 ? 
                newItems
                .filter(el => el.products[0].type.gender === "man")
                .map((el, id) => 
            <ItemCard 
                key={el.id}
                title={el.products[0].title}
                id={el.id}
                description={el.products[0].description}
                img={el.products[0].img}
                tovar={tovar}
                setTovar={setTovar}
                bye={true}
                price={el.products[0].price}
                alertState={alertState}
                alertSetState={alertSetState} />
            ) : <span>Loading...</span>}
      </ListWrap>
    </FlexColumn>
  )
}

const FlexColumn = styled.div`
  display: flex;
  flex-direction: column;
`
const ListWrap = styled.div`
  display: flex;
  flex-wrap: wrap;
`
export default ClothMan