import React,{ useState , useEffect } from 'react';
import { Routes, Route  } from "react-router-dom";
import styled from 'styled-components';
import ItemCard from '../../../CardOfItem/ItemCard'
import SortingCards from '../../SortingCards/SortingCards';
import ClothMan from './ClothMan.jsx/ClothMan';
import ClothWoman from './ClothWoman.jsx/ClothWoman';
import { useLocation } from "react-router";

const Clothes = ({
  wrapData,
  tovar,
  setTovar,
  alertState,
  alertSetState,
}) => {
  const [view , setView] = useState(true);

  const location = useLocation();

  
  useEffect(() => {
    if(location.pathname !== "/clothes"){
      setView(false)
    }
    if(location.pathname === "/clothes"){
      setView(true)
    }
  },[location])


return (
  <WrapForCardsPlusSorting>
      {view ?
            <SortingCards 
            wrapData={wrapData} 
            sortType={'clother'}/>

      : null}

        <Routes>
          <Route 
            path="/man" 
            element={
            <ClothMan
              wrapData={wrapData}
              alertState={alertState}
              alertSetState={alertSetState}
              tovar={tovar}
              setTovar={setTovar}
             />}/>
          <Route />
          <Route 
            path="/woman" 
            element={
            <ClothWoman
              wrapData={wrapData}
              alertState={alertState}
              alertSetState={alertSetState}
              tovar={tovar}
              setTovar={setTovar} />
            }/>
          <Route />
        </Routes>
  </WrapForCardsPlusSorting> 
)
}
const WrapForCardsPlusSorting = styled.div`
  padding: 10px 5px 150px 20px ;
`



export default Clothes