import React ,{ useState, useEffect, useContext, useMemo } from 'react';
import ContextData from '../../Context/ContextData';
import ContextTovar from '../../Context/ContextTovar';
import { useParams  } from "react-router-dom";
import GeneretedComponent from './GeneretedComponent/GeneretedComponent';

const {Provider} = ContextTovar;

const GeneretedLink = ({tovar , setTovar}) => {

  const [tovarContext , setTovarContext] = useState({});

  useEffect(() => {
      setTovarContext({tovar: tovar , setTovar : setTovar})
  },[tovar])


    let { id } = useParams();
    let data = useContext(ContextData);

    const currentItem = useMemo(() => {
      return data.filter(el => el.id == id)
    },[data , id])

    const [loading, setLoading] = useState(true);

    useEffect(() => {
      if(currentItem && currentItem.length > 0){
        setLoading(false)
      }
    },[data, currentItem])

  return (
    <>
    <Provider value={tovarContext}>
      {loading ?
        <span>Loading...</span> 
        :
        <GeneretedComponent
          img={currentItem[0].products[0].img}
          title={currentItem[0].products[0].title}
          id={currentItem[0].id}
          descr={currentItem[0].products[0].description}
          price={currentItem[0].products[0].price}
          typeItem={currentItem[0].products[0].type.type}
          typeGender={currentItem[0].products[0].type.gender}
       />
      }
    </Provider>
    </>
  )
}

export default GeneretedLink