import React from 'react';
import styled from 'styled-components';
import SidebarButton from './SidebarButton/SidebarButton';

const Sidebar = ({wrapData, setWrapData}) => {

  return (
    <WrapSidebar>
        <BtnWrapper>
            <SidebarButton
                text={'Бытовая техника'}
                linkWhere='tech'/>
            <SidebarButton
                text={'Одежда'}
                linkWhere='clothes'/>
            <SidebarButton
                text={'Товары для дома'}
                linkWhere='forHome'/>
            <SidebarButton
                text={'Еда'}
                linkWhere='food'/>
        </BtnWrapper>
    </WrapSidebar>
  )
}

export default Sidebar

const WrapSidebar = styled.div`
    min-width: 12.5rem;
    height:calc(100vh - 50px);
    position:sticky;
    left: 0;
    top:50px;
    padding: 0.625rem 0.3125rem ;
    border-right:1px solid #c8cfd8;
    @media screen and (max-width: 768px){
        display: none;
    }
`
const BtnWrapper = styled.ul`
    display: flex;
    flex-direction: column;
    width: 100%;
`