import React from 'react';
import styled,{css} from 'styled-components';

const ShadowScreen = ({setPodlojka, podlojka}) => {

  const removeShadow = () => {
    setPodlojka(false);
  }

  return (
    <>
      <DecorBox active={podlojka ? true : false}/>
    </>
    
  )
}

const DecorBox = styled.div((props) => {

  let opacityCheck = 0;
  let indexOfZ = 0;

  if(props.active === false){
    opacityCheck = 0;
    indexOfZ = -1;
  }else{
    opacityCheck = 1;
    indexOfZ = 8;
  }

return css`
  opacity:${opacityCheck};
  position: fixed;
  z-index:${indexOfZ};
  width: 100%;
  height:100vh;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0,0,0, .4);
  transition:all .3s;
`
})

export default ShadowScreen