import React,{ useState , useEffect } from 'react';
import styled, { css } from 'styled-components';
import { Link } from 'react-router-dom';

const SortingCards = ({wrapData, sortType }) => {

  const [sorted , setSorted] = useState([]);

  return (
  <>
      {sortType === "clother" ?
      <WrapCards>
        <Btn to="/clothes/man" variant={{variant: false}} >
        <Image typeImage={'man'}>
            <ShadowEffect />
          </Image>
          <Text>
              <span>Мужские вещи</span>
          </Text>

        </Btn> 

        <Btn to="/clothes/woman" variant={{variant: true}}>
          <Image typeImage={'woman'}>
            <ShadowEffect />
          </Image>
          <Text>
            <span>Женские вещи</span>
          </Text>
        </Btn>
    </WrapCards>
      
      : null}
  </>
  )
}


const WrapCards = styled.div`
  display: flex;
  flex-wrap:wrap;
`
const Btn = styled(Link)`
  margin: 0.25rem 0.25rem;
  overflow:hidden;
  display: flex;
  border-radius: 1.25rem;
  width: 300px;
  height: 200px;
  background-color: green;
  text-decoration: none;
  color:#fff;
  transition: all .3s;
  position: relative;
  :hover{
    div:first-of-type{
      transform: scale(1.05);
    }
  }
`
const Image = styled.div((props) => {

  let imgBg ;

  if(props.typeImage === "man"){
    imgBg = 'https://realist.online/img/article/1143/65_large-v1642764875.jpg'
  }else if(props.typeImage === "woman"){
    imgBg = 'https://vovk.com/image/cachewebp/catalog/new_general/00_december/ny26-770x550.1.0.webp'
  }
  return css`
    position:relative;
    transition: all .3s;
    border-radius: 1.25rem;
    padding: 0.9375rem;
    width: 100%;
    height: 100%;
    background-position: top;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(${imgBg});
  `;
});
  

const ShadowEffect = styled.div`
  position:absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background-color: rgba(1,1,1, 0.5);
  z-index: 2;
  border-radius: 1.25rem;
`
const Text = styled.span`
  border-radius: 1.25rem;
  z-index: 3;
  position:absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  padding: 0.625rem;
  font-size: 40px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: end;
`

export default SortingCards
