import React, { useEffect, useState , useContext } from "react";
import CardView from "./CardView/CardView";
import MiniCard from "../MiniCard/MiniCard";
import ContextTovar from "../../Context/ContextTovar";


const ItemCard = ({
  title,
  id,
  setTovar,
  tovar,
  bye,
  price,
  count,
  alertState,
  alertSetState,
  description,
  img
}) => {


  const [active, setActive] = useState(false);

  const ContextTovarData = useContext(ContextTovar);

  const [contextTovarFiltered , setContextTovarFiltered] = useState([{
    count: 1,
    id: 0,
    img: 'test',
    price: "1",
    title: "base title",
    description: "base descr"
  }]);

  useEffect(() => {
    setContextTovarFiltered((prev) => prev = ContextTovarData.tovar.filter(el => el.id === id));
  },[ContextTovarData])

  useEffect(() => {
    setActive(
      (prev) =>
        (prev = tovar.filter((el) => el.id === id).some((el) => el.id === id))
    );
  }, [tovar]);

  return (
    <>
      { bye ? (
          <CardView
            title={title}
            price={price}
            active={active}
            description={description}
            img={img}
            id={id}
          />
        ) : 
            (contextTovarFiltered.length > 0 ? <MiniCard
              
              removeFromBacket={true}
              image={contextTovarFiltered[0].img}
              title={contextTovarFiltered[0].title}
              price={contextTovarFiltered[0].price}
              id={contextTovarFiltered[0].id}
              count={contextTovarFiltered[0].count}
            /> : null
        )
      }
    </>
  );
};


export default ItemCard;
