import React, { useState, useEffect, useContext } from 'react';
import ContextTovar from '../Context/ContextTovar';
import Button from '@mui/material/Button';
import LocalGroceryStoreIcon from '@mui/icons-material/LocalGroceryStore';
import styled from 'styled-components';

const AddItemToBasket = ({
    title, 
    id,
    price,
    description,
    img
}) => {
    const getTovarContext = useContext(ContextTovar);
    const [activeBtn, setActiveBtn] = useState(true);
    const [loading, isLoading] = useState(true);

    let isCheck = false;

    useEffect(() => {
        isCheck = getTovarContext.tovar.some(el => el.id === id);
        if(isCheck === true){
            setActiveBtn(false);
        }else{
            setActiveBtn(true);
        }
        isLoading(false);
    },[getTovarContext])


    const AdditemFunc = () => {
        if(isCheck === true){
            return null
        }else{
            if(getTovarContext.tovar === null) {
                getTovarContext.setTovar(() => [{ title: title, id: id, price: price, count: 1, description: description , img : img }]);
            }else {
                getTovarContext.setTovar((prev) => [
                ...prev,
                { title: title, id: id, price: price, count: 1 , description: description , img : img },
                ]);
            }
        }
    }

    return(
        <> {loading ? null
            :  activeBtn ? 
            <ButtonStyle 
                onClick={AdditemFunc} 
                variant="contained" 
                aria-label="outlined primary button" 
                size="small">
                <LocalGroceryStoreIcon />
            </ButtonStyle>
                    :
            <ButtonStyle 
                    variant="contained" 
                    size="small"
                    disabled>
                buyed
            </ButtonStyle>}
        </>
    )
};
const ButtonStyle = styled(Button)`
    width: 40%;
    position:absolute;
    left:0.625rem;
    bottom:0.625rem;
    
    :hover{
        background-color: #d99b39 !important;
    }
`


export default AddItemToBasket