import React,{ useState, useContext } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';
import ContextTovar from '../../../Context/ContextTovar';
import AddItemToBasket from '../../../ActionButton/AddItemToBasket';
import { useInView } from "react-intersection-observer";
import Skeleton from '@mui/material/Skeleton';

const CardView = ({title,  price, active, description, img, id}) => {

    const { ref, inView } = useInView({
        threshold: 0.3,
        triggerOnce: true,
    });

    const tovarsContext = useContext(ContextTovar);

    const [hover , setHover] = useState(false);
    const imgUrl = img;

    const styleHover = {
        isHover: 
            {
                transform: `scale(1.1 , 1.04)`,
                zIndex: '2',

            }, 
        isNotHover: 
            {
            }
    };

    const setHoverOn = () => {
        setHover((prev) => prev = true);
    }
    const setHoverOff = () => {
        setHover((prev) => prev = false);
    }

    let image = '';

    const checkImsIsTru = () => {
        if(img){
            image = img;
        }else{
            image = 'https://static.vecteezy.com/system/resources/previews/005/337/799/original/icon-image-not-found-free-vector.jpg'
        }
    }

    checkImsIsTru();

    let textDescr = '';

    const StringLong = (str, count) => {       
        if(str.length > 40){
            textDescr = `${str.slice(0 , count)} ...`
        }
        if(str.length <= 0){
            textDescr = `Товар без описания`;
        }
    }
    hover ? StringLong(description, 130) : StringLong(description, 70);

  return (
    <WrapperCard 
        ref={ref}
        style={hover ? styleHover.isHover : styleHover.isNotHover}
        onMouseEnter={setHoverOn}
        onMouseLeave={setHoverOff}
        >
        {inView ? 
        <>
            <Container>
                <ContainerImg to={`/product/${id}`}>
                    <img src={image} alt="" />
                </ContainerImg>

                <ContainerContent>
                    <h4>{title}</h4>
                    <p>{textDescr}</p>
                    {hover? null :<span>{price}$</span>}
                </ContainerContent>
                {/* `${title.slice(0, 30)} ...` */}
            </Container>
            {hover ? 
            <HoverIsAdd>
                <AddItemToBasket 
                    title={title}
                    id={id}
                    price={price}
                    description={description}
                    img={img}
                />
            <p>{price}$</p>
            </HoverIsAdd>
            : null}
        </>
            :
            <RevorkSkeleton
                sx={{ bgcolor: 'grey.900' }}
                variant="rectangular"/>
        }

    </WrapperCard>
  )
}
const RevorkSkeleton = styled(Skeleton)`
    height: 100% !important;
    width: 100% !important;
`

const WrapperCard = styled.div`
    border-radius: 0.5rem;
    height: 26.25rem;
    width: 12.5rem;
    background-color: #fff;
    padding: 10px;
    margin: 0.3125rem;
    -webkit-box-shadow: 0px 5px 15px -3px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 5px 15px -3px rgba(0,0,0,0.75);
    box-shadow: 0px 5px 15px -3px rgba(0,0,0,0.75);
    transition: all .1s;
    position: relative;
`
const Container = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

`
const ContainerImg = styled(Link)`
        max-height: 12.5rem;
        min-height: 12.5rem;
        display: flex;
        justify-content:center;
        align-items:center;
        margin-bottom:0.5rem;
        :hover{
            cursor:pointer;
        }
    img{
        max-width: 100%;
        max-height: 100%;
    }
`
const ContainerContent = styled.div`
    display: flex;
    flex-direction: column;
    height: 100%;
    position: relative;
    h4 , p , span{
        margin-bottom:0.5rem;
        font-family: 'Roboto Mono', monospace;
    }
    h4 , p{
        width: 100%;
    }
    h4{
        font-weight: 700;
        font-size: 15px;
    }
    p{
        font-weight: 400;
        z-index:4;
        transition: all .05s;
        color:#3c3b39;
        font-size: 13px;
    }
    span{
        position: absolute;
        right: 0;
        bottom: 0;
        color: red;
        font-size: 1.375rem;
    }
`
const HoverIsAdd = styled.div`
    position:absolute;
    display: flex;
    width: 100%;
    height: 100px;
    background-color: #fff;
    left:0;
    bottom:-90px;
    transition: all .2s;
    z-index: 3;
    -webkit-box-shadow: 0px 12px 15px -3px rgba(0,0,0,0.75);
    -moz-box-shadow: 0px 12px 15px -3px rgba(0,0,0,0.75);
    box-shadow: 0px 12px 15px -3px rgba(0,0,0,0.75);
    border-radius: 8px;
    padding: 0.625rem;
    button{
        width: 40%;
        position:absolute;
        left:0.625rem;
        bottom:0.625rem;
        background-color: green;
        :hover{
            background-color: #d99b39;
        }
    }
    p{
        color: red;
        position:absolute;
        right:0.625rem;
        bottom:1rem;
        font-size: 1.375rem;
        font-family: 'Roboto Mono', monospace;
    }
`

export default CardView

