import React,{useEffect, useState} from 'react';
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Scrollbar, A11y , Autoplay  } from 'swiper';
import styled from 'styled-components';
import Skeleton from '@mui/material/Skeleton';

import 'swiper/css';
import 'swiper/css/navigation';
import 'swiper/css/scrollbar';
import 'swiper/css/autoplay';


import image01 from './SliderImages/305777846.jpg';
import image02 from './SliderImages/305865357.jpg';
import image03 from './SliderImages/306252641.jpg';

const SliderMain = () => {

  const [load, setLoad] = useState(true);

  useEffect(() => {
    setTimeout(() => setLoad(false), 500)
  },[])

  return (
    <Container >
      {load ?
      <RevorkSkeleton>Loading...</RevorkSkeleton>
            :
      <WrapSwiper
        modules={[Navigation, Scrollbar, A11y, Autoplay ]}
        spaceBetween={20}
        slidesPerView={1}
        navigation
        autoplay={{
          delay: 3000,
          disableOnInteraction: true
        }}
        speed={1400}
      >
        <WrapCard><img src={image01} alt="image" /></WrapCard>
        <WrapCard><img src={image02} alt="image" /></WrapCard>
        <WrapCard><img src={image03} alt="image" /></WrapCard>
      </WrapSwiper>
      }
    </Container>
  )
}
const RevorkSkeleton = styled.div`
    margin: 10px;
    width: 100%;
`
const WrapSwiper= styled(Swiper)`
    max-width: 100%;
    max-height: 400px;
    margin: 10px;
    height: auto;
`
const WrapCard = styled(SwiperSlide)`
width: 100%;
  img{
    max-width: 100%;
  }
`
const Container = styled.div`
  width: 100%;
  margin-bottom: 26px;
  @media only screen and (max-width: 420px) {
        display: none;
    }
`

export default SliderMain