import React, {useState, useEffect} from 'react';
import styled from 'styled-components';
import {Link} from 'react-router-dom';
import InputBase from '@mui/material/InputBase';
import SearchIcon from '@mui/icons-material/Search';
import Paper from '@mui/material/Paper';
import CloseIcon from '@mui/icons-material/Close';
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';

const Search = ({dataForHeader, setPodlojka}) => {
    const [inputValue, setInputValue] = useState('');
    const [focusInput, setFocusInput] = useState(false);

    let inputHandler = (e) => {
        let lowerCase = e.target.value.toLowerCase();
        setInputValue(lowerCase);
    };

    const filteredData = dataForHeader.filter((el) => {
        if (inputValue === '') {
            return '';
        }
        else {
            return el.products[0].title.toLowerCase().includes(inputValue)
        }
    })
    const setFocus = () => {
        setFocusInput(true)
    }

    useEffect(() => {
        if(inputValue.length >= 1){
            setPodlojka(true)
        }else{
            setPodlojka(false)
        }
    },[inputHandler])

    const ClearInputValue = () => {
        setInputValue(prev => prev = '');
    }

    return (
        <WrapForm>
            <FormWrap
                onFocus={setFocus}
                component="form"
                sx={{ p: '2px 4px', display: 'flex', alignItems: 'center'}}
                >
                <SearchIcon />
                <InputBase
                    value={inputValue}
                    onChange={inputHandler}
                    sx={{ ml: 1, flex: 1 }}
                    placeholder="Я ищу..."
                    inputProps={{ 'aria-label': 'search google maps' }}
            />

            {
                inputValue.length >= 1 ?
                <StyledButton onClick={ClearInputValue}>
                    <CloseIcon />
                </StyledButton>
                :null
            }

            </FormWrap>
            <DropDownList>

            {dataForHeader.length > 1 && filteredData.length !== 0? 
                filteredData.map((el) => <DropDownItem onClick={ClearInputValue}  to={`product/${el.id}`} key={el.products[0].id}><img src={el.products[0].img} />{el.products[0].title}<ArrowForwardIosIcon /></DropDownItem>)
                : null
            }

            {inputValue.length >= 1 && filteredData.length <= 0 && focusInput ?
             <EmptyTitle>По вашему запросу ничего не найдено...</EmptyTitle>
            : null}

            </DropDownList>
        </WrapForm>
    )
}
const StyledButton = styled.button`
    display: flex;
    justify-content: center;
    align-items: center;
    outline: none;
    background-color: #fff;
    border: none;
    border-left: 1px solid red;
    padding:2px 4px;
    :hover{
        cursor:pointer;
        svg{
            fill:red;
        }
    }
    svg{
        width: 20px;
        fill: #000;
    }
`
const EmptyTitle = styled.li`
    padding:6px 8px;
    background-color: #fff;
    border-bottom: 1px solid #ddd9d9;
    color: #000;
    text-decoration:none;
`
    

const WrapForm = styled.div`
    position: relative;
    z-index:8;
`
const DropDownList = styled.ul`
    z-index: 8;
    position: absolute;
    width: 100%;
    left:0;
    top:100%;
    background-color: red;
    display: flex;
    flex-direction: column;
    max-height: 400px;
    overflow-y:scroll;
    ::-webkit-scrollbar {
        width: 4px;
        background-color: #b4b3b3;
    }
    ::-webkit-scrollbar-thumb {
        background: #555;
    }
`
const DropDownItem = styled(Link)`
    padding:6px 8px;
    background-color: #fff;
    border-bottom: 1px solid #ddd9d9;
    color: #000;
    text-decoration:none;
    display: flex;
    align-items:center;
    padding-right: 28px;
    img{
        max-height: 40px;
        max-width:auto;
        margin-right:4px;
    }
    :hover{
        text-decoration:underline;
        background-color: #f0f0f0;
        svg{
            opacity: 1;
        }
    }
    svg{
        transition:all .2s;
        position:absolute;
        right: 4px;
        opacity: 0;
    }
`

const FormWrap = styled(Paper)`
    margin: 4px 2px;
    max-height: 40px;
    width: 400px;
    z-index:999;
    div{
        input{
            height: auto;
            font-size:14px;
        }
    }
    @media only screen and (max-width: 660px) {
        width: 300px;
    }
    @media only screen and (max-width: 420px) {
        width: 200px;
    }
`

export default Search