import React, { useContext, useState, useEffect } from 'react'
import Button from "@mui/material/Button";
import ButtonGroup from "@mui/material/ButtonGroup";
import styled from "styled-components";
import ContextTovar from '../Context/ContextTovar';
import DeleteOutlineIcon from '@mui/icons-material/DeleteOutline';

const RemoveItemFromBasket = ({price, id, count}) => {

    const [checkIncrementBtn , setCheckIncrementBtn] = useState(true);

    useEffect(() => {
      if(count >= 2){
        setCheckIncrementBtn(false)
      }
    },[])
    const [total , setTotal] = useState(0);

    const tovarContext = useContext(ContextTovar);

    const deletedBtn = () => {
        tovarContext.setTovar(tovarContext.tovar.filter((el) => el.id !== id));
    };
    
    const CounterBtnIncrement = () => {
        tovarContext.setTovar((prev) => {
          prev.forEach((el) => {
            if (el.id === id) {
              el.count = el.count - 1;
            }
            if (el.id === id && el.count <= 1) {
              setCheckIncrementBtn(true)
              // deletedBtn();
               //prev = tovarContext.tovar.filter((el) => el.id !== id);
            }
          });
          return [...prev];
        });
      };
    const CounterBtnDicrement = () => {
      setCheckIncrementBtn(false);
        tovarContext.setTovar((prev) => {
          prev.forEach((el) => {
            if (el.id === id) {
              el.count = el.count + 1;
            }
          });
          return [...prev];
        });
    };


    const TotalOfCurrentItem = () => {
      let calc = price * count;
      let total = `${calc}$/${count}`;
      setTotal((prev) => prev = total)
      
    }
    useEffect(() => {
      TotalOfCurrentItem();
    },[count])

  return (
    <>
        <WrapperBtnGroupWithInpust>
            <FlexDirectionColumn
                variant="contained"
                aria-label="outlined primary button group"
                size="small"
                >
            <Button
                onClick={() => deletedBtn()}
                style={{ backgroundColor: "red" , borderRight: "none", borderRadius: "0" }}
            >
                <DeleteOutlineIcon />
            </Button>
            
            <Button
                style={checkIncrementBtn ? {borderRight: "none",backgroundColor: "orange", cursor:'not-allowed'} : {
                borderRight: "none",
                backgroundColor: "yellow",
                color: "#000",
                }}
                onClick={checkIncrementBtn ? null
                  : () => CounterBtnIncrement()}
            >
                -
            </Button>
             <CountElementInCounter>
              {count}
            </CountElementInCounter> 
            <Button
                style={{ backgroundColor: "yellow", color: "#000" }}
                onClick={() => CounterBtnDicrement()}
            >
                +
            </Button>
            
            <span>{price}$</span>
        </FlexDirectionColumn>
        <WrapTotal>{total}</WrapTotal>
    </WrapperBtnGroupWithInpust>
    </>
    ) 
    
}

const FlexDirectionColumn = styled(ButtonGroup)`
    display: flex;
    flex-direction:column;
    button{
      margin: 2px;
    }
`
const CountElementInCounter = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 4px 0;
  text-align:center;
`;
const WrapTotal = styled.div`
  position: absolute;
  right: 0;
  bottom: 1.875rem;
  padding:2px;
`
const WrapperBtnGroupWithInpust = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  z-index: 4;
  padding: 2px 4px;
  span{
        font-size: 20px;
        color:#f84147;
        position: absolute;
        right: 0;
        bottom: 0;
        padding:2px;
    }
`;

export default RemoveItemFromBasket 