import React,{ useContext }from 'react';
import styled from 'styled-components';
import Button from '@mui/material/Button';
import LocalGroceryStoreIcon from '@mui/icons-material/LocalGroceryStore';
import Pagination from '../../CardsListItems/RoutesList/Pagination/Pagination';
import ContextTovar from '../../../Context/ContextTovar';
import AddItemToBasket from '../../../ActionButton/AddItemToBasket';

const GeneretedComponent = ({img, title, descr , price, typeItem, typeGender, id}) => {

    const tovarContext = useContext(ContextTovar);

  return (
    <div style={{overflow: 'hidden'}}>
        <Pagination
            typeCategory={typeItem}
            firstStep={'/'}
            secondStep={`/${typeItem}`}
            title={title}
            typeGender={typeGender} />
        <WrapFlex>
            <ContainerWrapLow>
                <img src={img} alt="image" />
            </ContainerWrapLow>

            <ContainerWrapTall>
                <MainTitle>{title}</MainTitle>
                <PriceText>{price} $</PriceText>
                <p>{descr}</p>
                <ContainerBtn>
                    <AddItemToBasket
                        title={title}
                        id={id}
                        price={price}
                        description={descr}
                        img={img}
                    />
                 </ContainerBtn>
            </ContainerWrapTall>
        </WrapFlex>
    </div>
  )
}
const WrapFlex = styled.div`
    display: flex;
    justify-content: space-between;
    padding:10px 6px;
`
const ContainerWrapLow = styled.div`
    width: 30%;
    padding: 10px 4px 10px 4px;
    img{
        max-width:100%;
        max-height: 300px;
    }
`
const ContainerWrapTall = styled(ContainerWrapLow)`
    width: 70%;
    padding-left:20px;
    display: flex;
    flex-direction: column;
`
const MainTitle = styled.p`
    font-size: 35px;
    font-weight: 700;
`
const PriceText = styled.p`
    width: 100%;
    color:red;
    font-size: 35px;
    text-align: end;
`
const ContainerBtn = styled.div`
    align-self: end;
    position: relative;
    max-width: 300px;
    min-width:300px;
    padding:10px 5px;
    display: flex;
    justify-content: end;
`

export default GeneretedComponent